<template>
    <!-- 不同种类文件导航 -->
    <div id="fileShow">
        <Title :title="CourseResourcesTitle" />
        <div class="file_navigation">
            <div class="w">
                <ul class="file_navigation_ul">
                    <li class="fl">
                        <FileShow title="教学课件" @click.native="goNJFUViewerPDF(0, '教学课件')" />
                    </li>
                    <li class="fl">
                        <FileShow title="课程教材及参考资料" @click.native="goNJFUViewerPDF(1, '课程教材及参考资料')" />
                    </li>
                    <li class="fl">
                        <FileShow title="案例分析" @click.native="goNJFUViewerPDF(2, '案例分析')" />
                    </li>
                    <li class="fl">
                        <FileShow title="教学视频" @click.native="goNJFUViewerVideo(3, '教学视频')" />
                    </li>
                    <li class="fl">
                        <FileShow title="习题库" @click.native="goNJFUViewerPDF(4, '习题库')" />
                    </li>
                    <li class="fl">
                        <FileShow title="其他电子资源" @click.native="goNJFUViewerPDF(5, '其他电子资源')" />
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import FileShow from '../components/FileShow.vue'
import Title from '../components/Title.vue'
export default {
    name: 'NJFUCourseResources',
    components: { FileShow, Title },
    props: ['CourseResourcesID', 'CourseResourcesTitle'],
    methods: {
        goNJFUViewerPDF(PDFID, PDFTitle) {
            this.$router.push({ name: 'ViewerPDF', query: { PDFID, PDFTitle } }, _ => { })
        },
        goNJFUViewerVideo(videoID, videoTitle) {
            this.$router.push({ name: 'ViewerVideo', query: { videoID, videoTitle } }, _ => { })
        }
    }
}
</script>

<style scoped>
.file_navigation {
    min-height: 350px;
    overflow: auto;
    padding-bottom: 20px;
}

.file_navigation_ul li {
    margin-top: 20px;
    margin-right: 40px;
}

.file_navigation_ul li:nth-child(6n) {
    margin-right: 0;
}
</style>