<template>
    <!-- 不同种类文件导航 -->
    <div id="fileShow">
        <Title :title="fileShowTitle" />
        <div class="file_navigation">
            <div class="w">
                <ul class="file_navigation_ul">
                    <li class="fl" v-for="(name, index) in files.file_name">
                        <FileShow :title="name" @click.native="goNJFUViewerPDF(index, name)" />
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import FileShow from '../components/FileShow.vue'
import Title from '../components/Title.vue'
export default {
    name: 'NJFUFileShow',
    components: { FileShow, Title },
    props: ['fileShowID', 'fileShowTitle'],
    data() {
        return {
            files: {
                file_picture: [],
                file_name: [],
                file_url: []
            }
        }
    },
    methods: {
        //file获取
        file_getJson() {
            axios.post(this.$HTTPIP + 'json/getFileJson', {
                fileShowID: this.fileShowID
            }).then(result => {
                console.log("file_getJson : ");
                console.log(result);
                this.files = result.data;
                if (this.files.file_name.length != this.files.file_url.length ||
                    this.files.file_name.length != this.files.file_picture.length) {
                    this.files = {};
                    alert('获取file信息失败! 请检查网络情况或联系管理员');
                    return;
                }
            }).catch(function (error) {
                console.log("file_getJson : ");
                console.log(error);
                alert('获取file信息失败! 请检查网络情况或联系管理员');
            })
        },
        goNJFUViewerPDF(PDFID, PDFTitle) {
            this.$router.push({ name: 'ViewerPDF', query: { PDFID, PDFTitle } })
        }
    },
    beforeMount() {
        // 检验数据合法性
        if (this.fileShowID === undefined || this.fileShowTitle === undefined)
            this.$router.replace({ name: 'Index' })
    },
    mounted() {
        console.log(this.fileShowTitle);
        if (this.fileShowID != null)
            this.file_getJson();
    }
}
</script>

<style scoped>
.file_navigation {
    min-height: 350px;
    overflow: auto;
    padding-bottom: 20px;
}

.file_navigation_ul li {
    margin-top: 20px;
    margin-right: 40px;
}
</style>